<template>
<div>
  <!---->
  <bread-crumb :list="item" v-for="(item,i) in breadCrumbList" :key="i"></bread-crumb>
  <el-card>
    <el-form :model="optForm" :rules="optRules" ref="optForm" label-width="100px" >
      <el-form-item label="">
        <el-upload
          :limit="this.limit"
          :action="upURL"
          list-type="picture-card"
          :on-success="handleAvatarSuccess"
          :on-remove="handleRemove"
          :file-list="fileList"
        >
          <i class="el-icon-plus"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="" prop="title">
        <el-input v-model="optForm.title" style="width: 20%"></el-input>
      </el-form-item>
      <el-form-item label="" prop="comment">
        <el-form-item label="">
          <el-switch v-model="optForm.comment"></el-switch>
        </el-form-item>
        <el-form-item label="">
          <el-switch v-model="optForm.review"></el-switch>
        </el-form-item>
        <el-button type="primary" style="margin-top: 20px" @click="upOpt"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</div>
</template>

<script>
import BreadCrumb from '@/components/BreadCrumb'
import { mapState } from 'vuex'
import { setting } from '@/api'
export default {
  name: 'Options',
  computed: {
    ...mapState(['upURL', 'host'])
  },
  components: {
    BreadCrumb
  },
  data () {
    return {
      breadCrumbList: [{
        breOne: '',
        breTwo: '',
        breThree: ''
      }],
      limit: 2,
      optForm: {
        title: '',
        comment: 'true',
        review: '',
        logo: ''
      },
      optRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ]
      },
      fileList: []
    }
  },
  created () {
    this.getOption()
  },
  methods: {
    async getOption () {
      const { data: res } = await this.$http.get(setting)
      if (!res._id) return this.$message.info('')
      this.optForm = res
      if (this.fileList.length === 0) { this.fileList.push({ url: `${this.host}` + res.logo }) }
    },
    handleAvatarSuccess (file) {
      this.fileList.shift()
      this.fileList.push({ url: `${this.host}` + file[0].file })
      this.optForm.logo = file[0].file
    },
    handleRemove () {
      this.optForm.logo = ''
    },
    async upOpt () {
      const { data: res } = await this.$http.post(setting, this.optForm)
      if (!res._id) return this.$message.error('')
      this.$message.success('')
      await this.getOption()
    }
  }
}
</script>

<style lang="less" scoped>
</style>
