<template>
  <div>
    <div>...? ? emm... </div>
    <div class="time">
      {{currentTime}}
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import moment from 'moment'
export default {
  name: 'Welcome',
  computed: {
    ...mapState(['userInfo'])
  },
  data () {
    return {
      currentTime: moment().format('YYYYMMDD E kk:mm:ss')
    }
  },
  created () {
    this.getTime()
  },
  methods: {
    getTime () {
      setInterval(() => {
        this.currentTime = moment().format('YYYYMMDD E kk:mm:ss')
      }, 1000)
    }
  }
}
</script>

<style lang="less" scoped>
.time{
  font-family: " Light";
  font-size: 25px;
}
</style>
