<template>
<div>
  <BreadCrumb :list="item" v-for="(item,i) in breadCrumbList" :key="i"></BreadCrumb>
  <el-card>
    <el-form :model="addOrEditForm" :rules="addOrEditFormRules" ref="addOrEditForm" label-width="100px" label-position="top">
      <el-row :gutter="30">
        <el-col :span="17">
          <el-form-item label="" prop="title">
            <el-input v-model="addOrEditForm.title"></el-input>
          </el-form-item>
          <el-form-item label="" prop="content">
            <quill-editor v-model="addOrEditForm.content"></quill-editor>
          </el-form-item>
        </el-col>
        <el-col :span="7" >
          <el-form-item label="">
            <el-upload
              :limit="this.limits"
              :action="upURL"
              list-type="picture-card"
              :on-success="handleAvatarSuccess"
              :on-remove="handleRemove"
              :file-list="fileList"
            >
              <i class="el-icon-plus"></i>
            </el-upload>
          </el-form-item>
          <el-row :gutter="20">
            <el-col :span="12" >
              <el-form-item label="" prop="category">
                <el-select
                  v-model="editFormCate"
                  @change="selectCate"
                  clearable
                >
                  <el-option
                    v-for="item in cateList"
                    :key="item._id"
                    :label="item.title"
                    :value="item._id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12" >
              <el-form-item label="" prop="state">
                <el-select
                  v-model="editFormState"
                  @change="selectState"
                  clearable
                >
                  <el-option
                    v-for="item in stateList"
                    :key="item.id"
                    :label="item.title"
                    :value="item.id"
                  >
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :gutter="20">
            <el-col :span="12">
              <el-form-item label="" prop="createAt">
                <el-date-picker
                  v-model="addOrEditForm.createAt"
                  type="datetime"
                  placeholder=""
                  :picker-options="pickerOptions"
                >
                </el-date-picker>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="updateAt">
                <el-date-picker
                  v-model="addOrEditForm.updateAt"
                  type="datetime"
                  placeholder=""
                  disabled
                >
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
          <template>
            <el-button type="primary" @click="editArticle"  v-if="editForm._id"></el-button>
            <el-button type="primary" @click="addArticle" v-else></el-button>
          </template>
          </el-col>
      </el-row>
    </el-form>
  </el-card>
</div>
</template>

<script>
import BreadCrumb from '@/components/BreadCrumb'
import { setStore } from '@/utils/storage'
import { getAllCategory, getAllPosts } from '@/api'
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'NewArticle',
  computed: {
    ...mapState(['userInfo', 'editForm', 'upURL', 'host'])
  },
  data () {
    return {
      limits: 2,
      fileList: [],
      showImg: false,
      breadCrumbList: [{
        breOne: '',
        breTwo: '',
        breThree: ''
      }],
      addOrEditForm: {
        author: '',
        title: '',
        state: '',
        content: '',
        category: '',
        cover: '',
        createAt: ''
      },
      imageUrl: '',
      addOrEditFormRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  50 ', trigger: 'blur' }
        ],
        createAt: [
          { required: true, message: '', trigger: 'blur' }
        ],
        category: [
          { required: true, message: '', trigger: 'blur' }
        ],
        state: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      dialogVisible: false,
      cateList: [],
      // 
      pickerOptions: {
        disabledDate (time) {
          return time.getTime() < Date.now()
        }
      },
      stateList: [{
        id: 0,
        title: ''
      }, {
        id: 1,
        title: ''
      }],
      limit: 1,
      editFormCate: '',
      editFormState: ''
    }
  },
  mounted () {
    this.getCateList()
    // 
    // 
    if (Object.keys(this.editForm).length !== 0) {
      this.addOrEditForm = this.editForm
      this.showImg = true
      // 
      this.editFormCate = this.editForm.category._id
      this.editFormState = this.editForm.state
      if (this.editForm.cover) this.fileList.push({ url: `${this.host}` + this.editForm.cover })
    }
  },
  components: {
    BreadCrumb
  },
  methods: {
    ...mapMutations(['CLEARFORM']),
    resetForm () {
      this.$refs.addOrEditForm.resetFields()
    },
    handleAvatarSuccess (file) {
      this.fileList.shift()
      this.fileList.push({ url: `${this.host}` + file[0].file })
      this.addOrEditForm.cover = file[0].file
    },
    handleRemove () {
      this.addOrEditForm.cover = ''
    },
    handlePictureCardPreview () {
      this.dialogImageUrl = this.imageUrl
      this.dialogVisible = true
    },
    async getCateList () {
      const { data: res } = await this.$http.get(getAllCategory)
      this.cateList = res
    },
    selectCate (id) {
      if (id === '') return
      this.addOrEditForm.category = id
    },
    selectState (id) {
      if (id === '') return
      this.addOrEditForm.state = id
    },
    addArticle () {
      this.$refs.addOrEditForm.validate(async v => {
        if (!v) return
        this.addOrEditForm.author = this.userInfo._id
        const { data: res } = await this.$http.post(getAllPosts, this.addOrEditForm)
        if (!res.meta) return this.$message.error(res.message)
        this.$message.success('')
        // index
        setStore('activePath', 'all-articles')
        this.$router.push({ name: 'allArticles' })
      })
    },
    async editArticle () {
      const id = this.addOrEditForm._id
      const { data: res } = await this.$http.put(getAllPosts + `/${id}`, this.addOrEditForm)
      if (!res.ok) return this.$message.error(res.message)
      this.$message.success('')
      // storeeditForm 
      this.CLEARFORM()
      setStore('activePath', 'all-articles')
      this.$router.push({ name: 'allArticles' })
    }
  }
}
</script>

<style lang="less" scoped>
.img{
  width: 300px;
  height: 200px;
}
.el-table {
  margin-top: 18px!important;
}
</style>
