<template>
<div>
  <!---->
  <bread-crumb :list="item" v-for="(item,i) in breadCrumbList" :key="i"></bread-crumb>
  <!---->
  <el-card>
    <!---->
    <el-row :gutter="40">
      <el-col :span="2">
        <!---->
        <template v-if="this.selectDelMany.length > 0 ">
          <el-button type="danger" @click="delMany"></el-button>
        </template>
      </el-col>
      <el-col :span="5">
        <el-input
          placeholder=""
          v-model="params.query"
          clearable
          @change="getUserList"
        >
          <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
        </el-input>
      </el-col>
      <el-col :span="10">
        <el-select
          v-model="statusList.state"
          placeholder=""
          @change="filtrateState"
          clearable
          style="width: 150px"
        >
          <el-option
            v-for="item in statusList"
            :key="item.val"
            :label="item.state"
            :value="item.val"
          >
          </el-option>
        </el-select>
        <el-select
          v-model="roleList.role"
          placeholder=""
          @change="filtrateRole"
          clearable
          style="width: 150px"
          class="margin-left7"
        >
          <el-option
            v-for="item in roleList"
            :key="item.val"
            :label="item.role"
            :value="item.val"
          >
          </el-option>
        </el-select>
        <el-button type="primary" @click="filtrate" class="margin-left7"></el-button>
      </el-col>
      <!---->
      <el-button type="primary" @click="addDialogVisible = true" style="float: left"></el-button>
    </el-row>
    <!---->
    <el-table
      ref="multipleTable"
      :data="userList"
      border
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
      v-loading="loading"
    >
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="title"
        label=""
        width="77"
      >
        <template v-slot="slot">
          <img :src="host + slot.row.avatar" alt="" v-if="slot.row.avatar">
          <img :src="host + '/assets/img/default.png'" alt="" v-else>
        </template>
      </el-table-column>
      <el-table-column
        prop="email"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="nickName"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="status"
        label=""
        width="200"
      >
        <template v-slot="slot">
          <el-tag v-if="slot.row.status ==0 " effect="dark" type="warning"></el-tag>
          <el-tag v-else effect="dark" type="success" ></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        prop="role"
        label=""
        width="200"
      >
        <template v-slot="slot">
          <el-tag effect="dark" v-if="slot.row.role === 'admin' " type="primary"></el-tag>
          <el-tag effect="plain" type="info" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label=""
      >
        <template slot-scope="scope">{{ scope.row.createTime | dateFormat}}</template>
      </el-table-column>
      <el-table-column
        label=""
        width="220"
      >
        <template v-slot="slot">
          <el-button type="primary" icon="el-icon-edit" circle @click="editUser(slot.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteUser(slot.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="params.page"
      :page-sizes="[4, 10, 20, 40]"
      :page-size="params.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      @close="resetForm"
      width="35%"
      >
      <el-form :model="addForm" :rules="addFormRules" ref="addForm" label-width="51px">
        <el-form-item label="">
          <template v-if="addForm.avatar == ''">
            <img :src="host + '/assets/img/default.png'" alt="" class="addImg">
          </template>
          <el-upload
            :limit="this.limit"
            :action="upURL"
            list-type="picture-card"
            :on-success="handleAvatarSuccess"
            :on-remove="handleRemove"
            :file-list="this.addFilesList">
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="addForm.nickName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password" >
          <el-input v-model="addForm.password" type="password"></el-input>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio border size="medium" v-model="addForm.status" label="0"></el-radio>
            <el-radio border size="medium" v-model="addForm.status" label="1"></el-radio>
          </template>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio border size="medium" v-model="addForm.role" label="normal"></el-radio>
            <el-radio border size="medium" v-model="addForm.role" label="admin"></el-radio>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="upAddUser"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="35%"
      @close="resetEditForm"
    >
      <el-form :model="editForm" :rules="addFormRules" ref="editForm" label-width="51px">
        <el-form-item label="">
          <template>
            <img
              v-show="this.defaultImg"
              :src=" host + '/assets/img/default.png'"
              class="addImg"
            >
            <img v-show="this.userImg" :src="host + editForm.avatar" class="addImg">
          </template>
          <el-upload
            :limit="this.limit"
            :action="upURL"
            list-type="picture-card"
            :on-success="handleAvatarSuccessEdit"
            :on-remove="handleRemoveEdit"
            :file-list="this.editFilesList"
           >
            <i class="el-icon-plus"></i>
          </el-upload>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="nickName">
          <el-input v-model="editForm.nickName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio border size="medium" v-model="editForm.status" label="0"></el-radio>
            <el-radio border size="medium" v-model="editForm.status" label="1"></el-radio>
          </template>
        </el-form-item>
        <el-form-item label="">
          <template>
            <el-radio border size="medium" v-model="editForm.role" label="normal"></el-radio>
            <el-radio border size="medium" v-model="editForm.role" label="admin"></el-radio>
          </template>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="upEditUser"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</div>
</template>

<script>
import BreadCrumb from '@/components/BreadCrumb'
import { mapState } from 'vuex'
import { getAllUsers } from '@/api'

export default {
  name: 'Users',
  computed: {
    ...mapState(['host', 'upURL'])
  },
  components: {
    BreadCrumb
  },
  data () {
    /*  */
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 100)
    }
    return {
      addFilesList: [],
      editFilesList: [],
      defaultImg: true,
      userImg: false,
      breadCrumbList: [{
        breOne: '',
        breTwo: '',
        breThree: ''
      }],
      userList: [],
      params: {
        query: '',
        page: 1,
        pagesize: 4
      },
      total: 0,
      loading: true,
      selectDelMany: '',
      addDialogVisible: false,
      editDialogVisible: false,
      addForm: {
        nickName: '',
        email: '',
        password: '',
        role: 'normal',
        avatar: '',
        status: '0'
      },
      editForm: {},
      addFormRules: {
        nickName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 50, message: ' 2  20 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, message: ' 2 ', trigger: 'blur' }
        ]
      },
      limit: 1,
      statusList: [{
        state: '',
        val: 1
      }, {
        state: '',
        val: 0
      }],
      roleList: [{
        role: '',
        val: 'admin'
      }, {
        role: '',
        val: 'normal'
      }],
      filterParams: {
        role: -1,
        status: -1
      }
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 
    handleSelectionChange (val) {
      const delIds = []
      for (const valElement of val) {
        delIds.push(valElement._id)
      }
      //  
      this.selectDelMany = delIds.join('-')
    },
    handleSizeChange (size) {
      this.params.pagesize = size
      this.getUserList()
    },
    handleCurrentChange (page) {
      this.params.page = page
      this.getUserList()
    },
    async getUserList () {
      const { data: res } = await this.$http.get(getAllUsers, { params: this.params })
      if (res.meta.status !== 200) return this.$message.error('')
      this.userList = res.user.records
      this.total = res.user.total
      this.loading = false
    },
    // 
    async delMany () {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(getAllUsers + `/${this.selectDelMany}`)
      if (!res.message) return this.$message.error('')
      this.$message.success('')
      await this.getUserList()
    },
    // 
    async deleteUser (row) {
      const id = row._id
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(getAllUsers + `/${id}`)
      console.log(res)
      if (!res) return this.$message.error('')
      this.$message.success('')
      await this.getUserList()
    },
    // 
    upAddUser () {
      this.$refs.addForm.validate(async v => {
        if (!v) return
        const { data: res } = await this.$http.post(getAllUsers, this.addForm)
        if (!res._id) return this.$message.error(res.message)
        this.addDialogVisible = false
        this.$message.success('')
        await this.getUserList()
      })
    },
    // 
    handleAvatarSuccess (file) {
      this.addForm.avatar = file[0].file
    },
    handleAvatarSuccessEdit (file) {
      this.editForm.avatar = file[0].file
      this.defaultImg = false
      this.userImg = false
    },
    handleRemove () {
      this.addForm.avatar = ''
    },
    handleRemoveEdit () {
      this.editForm.avatar = ''
      this.defaultImg = true
    },
    resetForm () {
      this.$refs.addForm.resetFields()
      this.addForm.avatar = ''
      this.addFilesList = []
    },
    resetEditForm () {
      this.editForm.avatar = ''
      this.editFilesList = []
    },
    // 
    async editUser (row) {
      /* this.editForm.avatar = row.avatar */
      const { _id } = row
      const { data: res } = await this.$http.get(getAllUsers + `/${_id}`)
      this.editForm = res
      this.editForm.status = res.status.toString()
      // 
      if (this.editForm.avatar !== '') {
        this.defaultImg = false
        this.userImg = true
      } else {
        this.defaultImg = true
        this.userImg = false
      }
      this.editDialogVisible = true
    },
    async upEditUser () {
      const { data: res } = await this.$http.put(getAllUsers + `/${this.editForm._id}`, this.editForm)
      if (res.nModified !== 1) return this.$message.error('')
      this.editDialogVisible = false
      this.$message.success('')
      await this.getUserList()
    },
    // 
    filtrateState (status) {
      //   
      if (status === '') {
        this.filterParams.status = -1
        this.filtrate()
        return
      }
      this.filterParams.status = status
    },
    filtrateRole (role) {
      if (role === '') {
        this.filterParams.role = -1
        this.filtrate()
        return
      }
      this.filterParams.role = role
    },
    // 
    async filtrate () {
      const { data: res } = await this.$http.get(getAllUsers, { params: this.filterParams })
      if (res.meta.status !== 200) this.$message.error(res.meta.msg)
      this.userList = res.user.records
      this.total = res.user.total
    }
  }

}
</script>

<style lang="less" scoped>
img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.addImg{
  width: 150px;
  height: 148px;
  border-radius: 0;
  float: left;
  margin-right: 10px;
}
.el-radio.is-bordered+.el-radio.is-bordered {
  margin-left: -10px;
}
</style>
