<template>
<div>
  <bread-crumb :list="item" v-for="(item,i) in breadCrumbList" :key="i"></bread-crumb>
  <el-card>
    <el-row :gutter="30">
      <el-col :span="2">
        <template v-if="this.selectDelMany.length > 0 ">
          <el-button type="danger" @click="delMany"></el-button>
        </template>
      </el-col>
      <el-col :span="4">
        <el-select
          v-model="stateList.state"
          placeholder=""
          @change="filtrateState"
          clearable
          style="width: 150px"
        >
          <el-option
            v-for="item in stateList"
            :key="item.val"
            :label="item.state"
            :value="item.val"
          >
          </el-option>
        </el-select>
        <el-button icon="el-icon-search" circle style="margin-left: 17px" @click="filtrate"></el-button>
      </el-col>
    </el-row>
    <el-table
      ref="multipleTable"
      :data="commentList"
      border
      stripe
      tooltip-effect="dark"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        type="selection"
        width="55">
      </el-table-column>
      <el-table-column
        prop="author.nickName"
        label=""
      >
      </el-table-column>
      <el-table-column
        prop="content"
        label=""
        width="160">
      </el-table-column>
      <el-table-column
        prop="post.title"
        label=""
        width="130"
      >
      </el-table-column>
      <el-table-column
        label=""
      >
        <template slot-scope="scope">{{ scope.row.createAt | dateFormat}}</template>
      </el-table-column>
      <el-table-column
        prop="author.status"
        label=""
        width="200"
      >
        <template v-slot="slot">
          <el-tag effect="plain" v-if="slot.row.state == 1 " type="success"></el-tag>
          <el-tag effect="dark" type="warning" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label=""
        width="220"
      >
        <template v-slot="slot">
          <el-button type="success" icon="el-icon-check" circle @click="allowComment(slot.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" circle @click="deleteOne(slot.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!---->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="params.page"
      :page-sizes="[4, 10, 20, 40]"
      :page-size="params.pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </el-card>
</div>
</template>

<script>
import { getAllComments } from '@/api'
import BreadCrumb from '@/components/BreadCrumb'
export default {
  name: 'Comments',
  components: {
    BreadCrumb
  },
  data () {
    return {
      breadCrumbList: [{
        breOne: '',
        breTwo: '',
        breThree: ''
      }],
      commentList: [],
      stateList: [{
        state: '',
        val: 1
      }, {
        state: '',
        val: 0
      }],
      total: 0,
      filterParams: {
        state: '-1'
      },
      params: {
        page: 1,
        pagesize: 4
      },
      selectDelMany: ''
    }
  },
  created () {
    this.getComments()
  },
  methods: {
    async getComments () {
      const { data: res } = await this.$http.get(getAllComments, { params: this.params })
      this.commentList = res.comment.records
      this.total = res.comment.total
    },
    filtrateState (state) {
      if (state === '') {
        //  
        this.filterParams.state = -1
        this.filtrate()
        return
      }
      this.filterParams.state = state
    },
    // 
    async filtrate () {
      const { data: res } = await this.$http.get(getAllComments, { params: this.filterParams })
      if (res.meta.status !== 200) this.$message.error(res.meta.msg)
      this.commentList = res.comment.records
      this.total = res.comment.total
    },
    handleSizeChange (size) {
      this.params.pagesize = size
      this.getComments()
    },
    handleCurrentChange (page) {
      this.params.page = page
      this.getComments()
    },
    // 
    handleSelectionChange (val) {
      const delIds = []
      for (const valElement of val) {
        delIds.push(valElement._id)
      }
      //  
      this.selectDelMany = delIds.join('-')
    },
    // 
    async delMany () {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(getAllComments + `/${this.selectDelMany}`)
      if (!res.message) return this.$message.error('')
      this.$message.success('')
      await this.getComments()
    },
    async deleteOne (row) {
      const id = row._id
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(getAllComments + `/${id}`)
      if (!res._id) return this.$message.error('')
      this.$message.success('')
      await this.getComments()
    },
    // 
    async allowComment (row) {
      if (row.state === 0) row.state = 1
      else row.state = 0
      const { _id } = row
      const { state } = row
      const { data: res } = await this.$http.put(getAllComments + `/${_id}`, { state })
      if (res.nModified !== 1) return this.$message.error('')
      this.$message.success('')
      await this.filtrate()
    }
  }
}
</script>

<style scoped>

</style>
