<template>
<div>
  <!---->
  <bread-crumb :list="item" v-for="(item,i) in breadCrumbList" :key="i"></bread-crumb>
  <!---->
  <el-card>
    <template v-if="this.selectDelMany.length > 0 ">
      <el-button type="danger" @click="delMany" style="margin-right: 30px"></el-button>
    </template>
    <el-button type="primary" @click="addDialogVisible = true"></el-button>
    <!---->
    <el-table
          :data="cateList"
          stripe
          style="width: 100%"
          @selection-change="handleSelectionChange"
         >
          <el-table-column
            type="selection"
            width="55">
          </el-table-column>
          <el-table-column
            prop="title"
            label=""
           >
          </el-table-column>
          <el-table-column
            prop="className"
            label=""
          >
          </el-table-column>
          <el-table-column
            label=""
          >
            <template slot-scope="scope">{{ scope.row.createAt | dateFormat}}</template>
          </el-table-column>
          <el-table-column
            label=""
            width="180"
           >
            <template v-slot="slot">
              <el-button type="primary"  icon="el-icon-edit" circle @click="editCate(slot.row)"></el-button>
              <el-button type="danger"  icon="el-icon-delete" circle @click="deleteCate(slot.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addDialogVisible"
      width="40%"
      @close="resetForm"
    >
      <el-form
        :model="cateForm"
        :rules="cateFormRules"
        ref="cateForm"
        label-width="100px"
      >
        <el-form-item label="" prop="title" >
          <el-input v-model="cateForm.title" style="width: 97%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="className">
          <el-input v-model="cateForm.className" style="width: 97%" ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="upCateForm"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="40%"
      @close="resetForm"
    >
      <el-form
        :model="editCateForm"
        :rules="cateFormRules"
        ref="cateForm"
        label-width="100px"
      >
        <el-form-item label="" prop="title">
          <el-input v-model="editCateForm.title" style="width: 97%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="className">
          <el-input v-model="editCateForm.className" style="width: 97%"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="modifyCateForm"> </el-button>
      </span>
    </el-dialog>
  </el-card>
</div>
</template>

<script>
import { getAllCategory } from '@/api'
import BreadCrumb from '@/components/BreadCrumb'
export default {
  name: 'article-category',
  components: {
    BreadCrumb
  },
  data () {
    return {
      breadCrumbList: [{
        breOne: '',
        breTwo: '',
        breThree: ''
      }],
      cateForm: {
        title: '',
        className: ''
      },
      editCateForm: {
        title: '',
        className: ''
      },
      cateFormRules: {
        title: [
          { required: true, message: '', trigger: 'blur' },
          { min: 2, max: 30, message: ' 2  30 ', trigger: 'blur' }
        ],
        className: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      cateList: [],
      addDialogVisible: false,
      editDialogVisible: false,
      selectDelMany: ''
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$http.get(getAllCategory)
      this.cateList = res
    },
    resetForm () {
      this.$refs.cateForm.resetFields()
    },
    upCateForm () {
      this.$refs.cateForm.validate(async v => {
        if (!v) return
        const { data: res } = await this.$http.post(getAllCategory, this.cateForm)
        if (!res._id) return this.$message.error(res.message)
        this.addDialogVisible = false
        this.$message.success('')
        await this.getCateList()
      })
    },
    // 
    async editCate (row) {
      const { _id } = row
      const { data: res } = await this.$http.get(getAllCategory + `/${_id}`)
      this.editDialogVisible = true
      this.editCateForm = res
    },
    // 
    async modifyCateForm () {
      const { _id } = this.editCateForm
      const { data: res } = await this.$http.put(getAllCategory + `/${_id}`, this.editCateForm)
      if (!res.ok) return this.$message.error(res.message)
      this.editDialogVisible = false
      this.$message.success('')
      await this.getCateList()
    },
    // 
    async deleteCate (row) {
      const confirmResult = await this.$confirm(' ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { _id } = row
      const { data: res } = await this.$http.delete(getAllCategory + `/${_id}`)
      if (res.message !== '') return this.$message.error(res.message)
      this.$message.success('')
      await this.getCateList()
    },
    // 
    handleSelectionChange (val) {
      const delIds = []
      for (const valElement of val) {
        delIds.push(valElement._id)
      }
      //  
      this.selectDelMany = delIds.join('-')
    },
    // 
    async delMany () {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(getAllCategory + `/${this.selectDelMany}`)
      if (!res) return this.$message.error('')
      this.$message.success('')
      await this.getCateList()
    }
  }
}
</script>

<style scoped>

</style>
